<template>
	<div class="buy">
		<van-nav-bar
			  fixed
			  border
			  title="积分充值"
			  left-arrow
			  @click-left="goBack"
			/>
		<div class="buy_cent">
			<p class="buy_title">订单信息</p>
			<div class="buy_info">
				<p v-if="id == 1"><span>商品名称：</span><span>开通VIP</span></p>
				<p v-else><span>商品名称：</span> <span>积分充值</span></p>
				<p><span>订单编号：</span><span>{{order_sn}}</span></p>
				<p><span>商家名称：</span><span>天集社</span></p>
				<p><span>支付金额：</span><span>￥{{price}}</span></p>
			</div>
		</div>
		<div class="payment">
			<p class="buy_title">支付方式</p>
			<van-button type="warning" size="large" block @click="apy">微信支付</van-button>
		</div>
	</div>
		
</template>

<script>
import wx from "weixin-js-sdk";
import {Col, Row,NavBar,Cell, CellGroup,Button,Field } from "vant";		
import {createOrder} from "@/api/v4.js"
import {getJSSDKSign} from "@/api/base.js"
export default{
	components:{
		[NavBar.name]: NavBar,
		[Col.name]: Col,
		[Row.name]: Row,
		[Cell.name]: Cell,
		[CellGroup.name]: CellGroup,
		[Button.name]: Button,
		[Field.name]: Field,
		 
	},
	data(){
		return{
			value:"1600",
			order_sn:null,
			id:null,
			price:null,
			type:null
		}
	},
	created() {
		this.id = this.$route.query.odId
		this.type = this.$route.query.type
		this.price = this.$route.query.price
		this.order_sn = this.$route.query.order_sn
	},
	methods:{
		goBack() {
			this.$router.back();
		},
		apy(){
			var ua = navigator.userAgent.toLowerCase();
			if(ua.match(/MicroMessenger/i)=="micromessenger") {
				if (/(iPhone|iPad|iPod|iOS|Android)/i.test(navigator.userAgent)) { //移动端
					createOrder({
						order_sn:this.order_sn,
						id:this.id,
						type:this.type
					}).then(res=>{
						let a = JSON.parse(res.order_str)
						let than = this
						 WeixinJSBridge.invoke(
						       'getBrandWCPayRequest',{
						          "appId": a.appId,     
						          "timeStamp": a.timeStamp,         
						          "nonceStr": a.nonceStr,      
						          "package": a.package,     
						          "signType":a.signType,        
						          "paySign":a.paySign
						       },
						       function(res){
						       if(res.err_msg == "get_brand_wcpay_request:ok" ){
								   than.$dialog.alert({
									   title:"温馨提示",
										message:"支付成功"
									})
									than.$router.replace("/integral/index")
						       }else if(res.err_msg=="get_brand_wcpay_request:cancel"){
								   than.$router.replace("/integral/recharge")
							   }else{
								   than.$dialog.alert({
									   title:"温馨提示",
										message:"支付失败"
									})
									than.$router.replace("/integral/recharge")
							   } 
						    }); 
					})
				}else{
					this.$dialog.alert({
						title:"温馨提示",
						message:"请用手机进入平台支付"
					})
				}
			} else {
				this.$dialog.alert({
					title:"温馨提示",
					message:"请用手机进入平台支付"
				})
			}
			
		}
	}
}		
</script>

<style scoped>
.buy{
	background-color: #F6F6F6;
	margin-top:5px;
}
.buy_cent{
	background-color: #fff;
	padding:0 0 30px 0;
}
.buy_title{
	margin:0px 0 24px 15px;
	padding-top:24px;
	font-size:17px;
	font-weight:bold;
	color:rgba(51,51,51,1);
}
.buy_info{
	width:90%;
	margin:0 auto;
	box-shadow:0px 3px 6px 0px rgba(0, 0, 0, 0.16);
	border-radius:0px 0px 7px 7px;
	padding:10px 0;
	
}
.buy_info p{
	margin:15px 15px;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
}
.buy_info p span:last-child{
	color: #999;
	white-space: pre-wrap;
}
.payment{
	background-color: #fff;
	margin-top: 10px;
	padding-bottom:1px;
}
.van-button--large{
	width: 90%;
	margin:20px auto;
	
}
.van-button--warning {
    color: #fff;
    background-color: #CB0423;
    border: 1px solid #CB0423;
}
.van-button{
	border-radius: 10px;
}
@media (min-width:750px) {
	.buy{
		background-color: #F6F6F6;
		margin-top:5px;
	}
	.buy_cent{
		background-color: #fff;
		padding:0 0 30px 0;
	}
	.buy_title{
		margin:0px 0 24px 15px;
		padding-top:24px;
		font-size:17px;
		font-weight:bold;
		color:rgba(51,51,51,1);
	}
	.buy_info{
		width:90%;
		margin:0 auto;
		box-shadow:0px 3px 6px 0px rgba(0, 0, 0, 0.16);
		border-radius:0px 0px 7px 7px;
		padding:10px 0;
		
	}
	.buy_info p{
		margin:15px 15px;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
	}
	.buy_info p span:last-child{
		color: #999;
		white-space: pre-wrap;
	}
	.payment{
		background-color: #fff;
		margin-top: 10px;
		padding-bottom:1px;
	}
	.van-button--large{
		width: 90%;
		margin:20px auto;
		
	}
	.van-button--warning {
	    color: #fff;
	    background-color: #CB0423;
	    border: 1px solid #CB0423;
	}
	.van-button{
		border-radius: 10px;
	}
}
</style>
